<template>
  <view class="cardBox">
    <view class="top">
      <view class="img-top">
        <view class="img-left">
          <view class="img-zou">
            <view class="iconfont icon-genwozou genwozou"></view> 商品热度{{
              heat_number
            }}
          </view>
        </view>
        <view class="img-right">
          <view class="iconfont icon-v"></view>
        </view>
      </view>
      <view class="love" :animation="loveAnimation[0]">
        <view
          class="iconfont icon-xinaixin"
          :style="{ fontSize: '60rpx' }"
        ></view>
      </view>
      <view class="loathe" :animation="loatheAnimation[0]">
        <view
          class="iconfont icon-chacha1"
          :style="{ fontSize: '60rpx' }"
        ></view>
      </view>
      <image class="img" :src="src"></image>
    </view>

    <view class="bottom">
      <view class="star">
        <view
          class="iconfont icon-wujiaoxing1"
          :style="{ fontSize: '60rpx' }"
        ></view>
      </view>
      <view>{{ goods_name }}</view>
      <view class="labelBox">
        <view class="label" :style="{ backgroundColor: '#A4C742' }">{{
          shop_price
        }}</view>
        <view class="label" :style="{ backgroundColor: '#7BD8FF' }">{{
          goods_number
        }}</view>
      </view>

      <!-- <view class="address">{{address}}</view> -->
    </view>
  </view>
</template>

<script>
export default {
  props: {
    goods_id: {
      type: Number,
      default: 0,
    },
    src: {
      type: String,
      default: "",
    },
    address: {
      type: String,
      default: "",
    },
    goods_name: {
      type: String,
      default: "",
    },
    shop_price: {
      type: String,
      default: "",
    },
    number: {
      type: Number,
      default: 1,
    },
    heat_number: {
      type: Number,
      default: 1,
    },
    goods_number: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      loveAnimation: { 0: {} },
      loatheAnimation: { 0: {} },
      loveAni: null,
      loatheAni: null,
    };
  },
  mounted() {
    //touch移动喜欢动画
    this.loveAni = uni.createAnimation({
      duration: 0,
    });
    //touch移动不喜欢动画
    this.loatheAni = uni.createAnimation({
      duration: 0,
    });
  },
  methods: {
    clearAnimation() {
      this.loveAnimation[0] = this.loveAni.export();
      this.loatheAnimation[0] = this.loatheAni.export();
    },
    _back() {
      //touch移动喜欢动画
      this.loveAni = uni.createAnimation({
        duration: 200,
      });
      //touch移动不喜欢动画
      this.loatheAni = uni.createAnimation({
        duration: 200,
      });
      this.loveAni.opacity(0).step();
      this.loveAnimation[0] = this.loveAni.export();
      this.loatheAni.opacity(0).step();
      this.loatheAnimation[0] = this.loatheAni.export();
    },
    moveRight(ratio) {
      this.loveAni.opacity(0.3 + 0.7 * ratio).step();
      this.loveAnimation[0] = this.loveAni.export();
      this.loatheAni.opacity(0).step();
      this.loatheAnimation[0] = this.loatheAni.export();
    },
    moveLeft(ratio) {
      this.loveAni.opacity(0).step();
      this.loveAnimation[0] = this.loveAni.export();
      this.loatheAni.opacity(0.3 + 0.7 * ratio).step();
      this.loatheAnimation[0] = this.loatheAni.export();
    },
    moveCenter() {
      this.loveAni.opacity(0.3).step();
      this.loveAnimation[0] = this.loveAni.export();
      this.loatheAni.opacity(0.3).step();
      this.loatheAnimation[0] = this.loatheAni.export();
    },
  },
  watch: {
    cardData(newVal) {
      console.log(newVal);
      this.item = newVal;
    },
  },
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "iconfont";
  src: url("data:application/x-font-woff2;charset=utf-8;base64,")
    format("woff2");
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 12px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-v:before {
  content: "\e670";
}

.icon-male:before {
  content: "\e600";
}

.icon-wujiaoxing1:before {
  content: "\e602";
}

.icon-chacha1:before {
  content: "\e646";
}

.icon-genwozou:before {
  content: "\e61d";
}

.icon-xinaixin:before {
  content: "\e601";
}

.icon-tupian1:before {
  content: "\e718";
}

.icon-xingbie-nv:before {
  content: "\e677";
}

.cardBox {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #fff;
  border-radius: 20rpx;
  border: 2px solid #e9e7ef;
  .top {
    width: 600rpx;
    height: 600rpx;
    .img-top {
      position: absolute;
      font-size: 12px;
      top: 20rpx;
      padding: 0 20rpx;
      display: flex;
      width: 560rpx;
      flex-direction: row;
      justify-content: space-between;
      z-index: 999;
      .img-left {
        display: flex;
        flex-direction: row;
        align-items: center;
        color: #fff;
        .img-number {
          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 10rpx;
          background-color: rgba(0, 0, 0, 0.2);
          border-radius: 10rpx;
          margin-right: 20rpx;
          .tupian {
            margin-right: 5rpx;
          }
        }
        .img-zou {
          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 10rpx;
          background-color: rgba(0, 0, 0, 0.4);
          border-radius: 10rpx;
          .genwozou {
            margin-right: 20rpx;
          }
        }
      }
      .img-right {
        width: 50rpx;
        height: 50rpx;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #54c7fc;
        color: #ffffff;
      }
    }
    .img {
      width: 600rpx;
      height: 600rpx;
    }
    .love {
      position: absolute;
      width: 120rpx;
      height: 120rpx;
      top: 60rpx;
      left: 40rpx;
      border-radius: 50%;
      background-color: #ea2a36;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 999;
      color: #ffffff;
      opacity: 0;
    }
    .loathe {
      position: absolute;
      width: 120rpx;
      height: 120rpx;
      top: 60rpx;
      right: 40rpx;
      border-radius: 50%;
      background-color: #a09e9f;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 999;
      color: #ffffff;
      opacity: 0;
    }
  }
  .bottom {
    position: relative;
    display: flex;
    padding: 10rpx 20rpx;
    flex-direction: column;
    color: #202020;
    .labelBox {
      margin-top: 30rpx;
      display: flex;
      flex-direction: row;
      .label {
        padding: 10rpx;
        font-size: 12px;
        color: #fff;
        display: flex;
        flex-direction: row;
        border-radius: 8rpx;
        margin-right: 15rpx;
      }
      .sex {
        margin-right: 5rpx;
      }
    }

    .address {
      margin-top: 10rpx;
      font-size: 12px;
      color: #b9b9b9;
    }
    .star {
      position: absolute;
      width: 120rpx;
      height: 120rpx;
      top: -60rpx;
      right: 50rpx;
      border-radius: 50%;
      background-image: linear-gradient(to bottom left, #87deff, #1faeff);
      display: flex;
      justify-content: center;
      align-items: center;

      color: #ffffff;
    }
  }
}
</style>
